.burguer-menu {
    position: absolute;
    display: none;
    right: 0;
    top: 50%;
    margin-top: -7px;
    text-indent: -40000px;
    z-index: 200;
    opacity: 1;
    border-radius: 100%;
    background-color: transparent;
}

.burguer-menu .burguer-menu__containment {
    position: relative;
    width: 20px;
    height: 14px;
    -webkit-transition-delay: .5s;
    transition-delay: .5s;
    background-color: transparent;
}

.burguer-menu .burguer-menu__containment div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    transition: all .3s ease;
    -webkit-transition-delay: .4s;
    transition-delay: .4s;
    background-color: transparent;
}

.burguer-menu .burguer-menu__containment div span,
.burguer-menu .burguer-menu__containment div::before,
.burguer-menu .burguer-menu__containment div::after {
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #fff;
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    transition: all .3s ease;
    left: 50%;
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

.burguer-menu .burguer-menu__containment div span {
    top: 50%;
    margin-top: -1px;
    -webkit-transition-delay: .3s;
    transition-delay: .3s;
}

.burguer-menu .burguer-menu__containment div::before {
    content: " ";
    top: 0;
    -webkit-transition-delay: .01s;
    transition-delay: .01s;
}

.burguer-menu .burguer-menu__containment div::after {
    content: " ";
    bottom: 0;
    -webkit-transition-delay: .01s;
    transition-delay: .01s;
}

header.menu-opened .burguer-menu .burguer-menu__containment div {
    top: 50%;
    height: 2px;
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    transition: all .3s ease;
    -webkit-transition-delay: .0s;
    transition-delay: .0s;
    margin-top: -1px;
}

header.menu-opened .burguer-menu .burguer-menu__containment div::before,
header.menu-opened .burguer-menu .burguer-menu__containment div::after {
    -webkit-transition-delay: .4s;
    transition-delay: .4s;
    background-color: #fff;
}

header.menu-opened .burguer-menu .burguer-menu__containment div span {
    opacity: 0;
    -webkit-transition-delay: .0s;
    transition-delay: .0s;
}

header.menu-opened .burguer-menu .burguer-menu__containment div::before {
    -moz-transform: translateX(-50%) rotate(-45deg);
    -ms-transform: translateX(-50%) rotate(-45deg);
    -webkit-transform: translateX(-50%) rotate(-45deg);
    transform: translateX(-50%) rotate(-45deg);
}

header.menu-opened .burguer-menu .burguer-menu__containment div::after {
    -moz-transform: translateX(-50%) rotate(45deg);
    -ms-transform: translateX(-50%) rotate(45deg);
    -webkit-transform: translateX(-50%) rotate(45deg);
    transform: translateX(-50%) rotate(45deg);
}

