.steps {
    background-color: #00A7FF;
    width: 100%;
    position: relative;
}

.stepsContent {
    margin: auto;
    max-width: 1200px;
    width: 100%;
    padding-top: 6rem;
}

.stepsContent h1 {
    text-align: center;
    font-size: 70px;
    position: relative;
    font-weight: 700;

}

.textCenter {
    text-align: center;
    font-size: 22px;
    margin: 5px 0px;
}

.imgNuvem {
    position: absolute;
    right: 0%;
    top: -120px;
    width: 40%;
}

.imgAviao {
    position: absolute;
    left: 0%;
    top: -120px;
    width: 300px;
}

.stepByStep {
    display: grid;
    gap: 15px;
    margin: 6rem auto 0px auto;
    grid-template-columns: repeat(4, 1fr);
    width: 100%;
    position: relative;
    z-index: 2;
    counter-reset: stepCount 1;
}

.stepByStepSingle {
    background-color: #0c248b;
    padding: 30px;
    text-align: center;
    font-size: 20px;
    border: 3px solid white;
    border-radius: 10px;
    position: relative;
}

.stepByStepSingle span {
    display: block;
    margin-top: 10px;
    margin-bottom: 5px;
    color: #00A7FF;
}

.stepByStepSingle p {
    margin-top: 20px;
    font-weight: 600;
}

.stepByStepSingle a {
    padding: 12px 18px;
    border: 2px solid white;
    border-radius: 10px;
    text-transform: uppercase;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    font-weight: 600;
    margin-top: 15px;
    transition: ease-in-out 0.3s;
}

.stepByStepSingle a:hover {
    background-color: #00A7FF;
}

.stepByStepSingle::before {
    content: counter(stepCount);
    position: absolute;
    left: 50%;
    top: -60px;
    transform: translate(-50%, 0px);
    height: 90px;
    width: 90px;
    border-radius: 50%;
    background-color: #00A7FF;
    border: 4px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 70px;
    color: white;
    font-weight: 700 !important;
}

/* Incrementa o contador e insere um crculo vazio aps cada item */
.stepByStepSingle::after {
    counter-increment: stepCount;
    content: '';
}

