.textanim p,
.textanim h1,
.textanim h2 {
    position: relative;
    overflow: hidden;
}

.textanim p .text,
.textanim h1 .text,
.textanim h2 .text {
    display: block;
    animation: textanimation 1s backwards;
    animation-delay: 400ms;
}

p:nth-child(1) .text {
    animation-delay: 400ms;
}

h2:nth-child(2) .text {
    animation-delay: 600ms;
}

