.modal {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
}

.modal__backdrop {
    background-color: #000;
    width: 100%;
    height: 100%;
    opacity: .65;
}

.modal__close {
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 30px;
    margin: 10px 10px 0 0;
    border-radius: 100%;
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    text-indent: -40000px;
}

.modal__close::after {
    content: "";
    font-size: 3em;
    color: #fff;
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-indent: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.modal__content {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}

.modal__wrapper {
    position: relative;
    display: table;
    max-width: 90%;
    margin: 0 auto;
    box-shadow: 0px 0px 6px 6px rgba(0, 0, 0, 0.05);
}

.modal__wrapper img {
    width: 100%;
    height: auto;
}

